/* Слайдер */
.slick-slider {
  min-width: 0; }

/* Ограничивающая оболочка */
.slick-list {
  overflow: hidden; }

/* Лента слайдов */
.slick-track {
  display: flex;
  align-items: flex-start; }

/* Конкретный слайдер */
.slider {
  position: relative;
  padding: 0px 60px; }

::selection {
  background-color: #7fc7ff;
  color: #ffffff; }

.page {
  min-height: 100%;
  font-size: 10px;
  font-family: "Oswald Regular", "Arial", sans-serif; }

.body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000; }

.hidden {
  display: none !important; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Blocks */
.wrapper {
  position: relative;
  min-width: 280px;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  width: 80%;
  margin-top: 25vh;
  padding: 5em 0 0;
  margin-bottom: 25vh; }
  @media (min-width: 1040px) {
    .wrapper {
      display: grid;
      width: 80%;
      grid-template-columns: repeat(3, 280px);
      justify-content: space-between;
      grid-row-gap: 4em; } }
  @media (min-width: 1200px) {
    .wrapper {
      grid-template-columns: repeat(4, 240px); } }
  @media (min-width: 1400px) {
    .wrapper {
      grid-template-columns: repeat(4, 280px); } }
  @media (min-width: 1800px) {
    .wrapper {
      grid-template-columns: repeat(4, 320px); } }

.wrapper > .slick-list > .slick-track {
  position: relative;
  min-width: 280px;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box; }

.news-block::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border: 2px solid #222222;
  box-sizing: border-box; }

.news {
  position: relative;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 240px;
  width: 320px;
  height: 472px;
  margin-right: 2em;
  margin-bottom: 2em;
  transition: opacity 0.2s, bottom 0.5s;
  opacity: 1;
  cursor: pointer; }
  .news:hover {
    opacity: 0.8;
    bottom: 4%; }
  .news:active {
    opacity: 0.6; }
  @media (max-width: 320px) {
    .news {
      width: 280px; } }
  @media (min-width: 768px) {
    .news {
      width: 300px;
      margin-right: 1.5em; } }
  @media (min-width: 1040px) {
    .news {
      width: 280px;
      margin-right: 0;
      margin-bottom: 0; } }
  @media (min-width: 1200px) {
    .news {
      width: 240px; } }
  @media (min-width: 1400px) {
    .news {
      width: 280px; } }
  @media (min-width: 1800px) {
    .news {
      width: 320px; } }

.news-block__img {
  display: block;
  user-select: none;
  transition: transform 0.2s; }
  @media (max-width: 320px) {
    .news-block__img {
      width: 280px; } }
  @media (min-width: 768px) {
    .news-block__img {
      width: 300px; } }
  @media (min-width: 1040px) {
    .news-block__img {
      width: 280px; } }
  @media (min-width: 1200px) {
    .news-block__img {
      width: 240px; } }
  @media (min-width: 1400px) {
    .news-block__img {
      width: 280px; } }
  @media (min-width: 1800px) {
    .news-block__img {
      width: 320px; } }

.news:hover .news-block__img {
  transform: rotate(4deg); }

.news__section {
  display: flex;
  align-items: center;
  margin-top: 1em;
  color: #b2544f;
  font-size: 1.6em;
  font-family: "Philosopher-Regular", "Arial", sans-serif;
  font-weight: bold; }

.deco-img {
  color: #717171;
  transition: color 0.2s;
  user-select: none; }

.news:hover .deco-img {
  color: #b2544f; }

.news__title {
  position: relative;
  width: 100%;
  color: #222222;
  font-family: "Philosopher-Bold", "Arial", sans-serif;
  font-weight: bold;
  font-size: 1.9em; }

.news__content {
  font-family: "NotoSerif-Regular", "Arial", sans-serif;
  font-size: 1.4em;
  color: gray;
  margin-top: 0;
  overflow-y: scroll; }
  .news__content::-webkit-scrollbar {
    width: 0px; }

.news__publication-time {
  color: #b3b3b3;
  font-size: 1.1em; }
